html,
body {
    width: 100%;
    height: 100%;
}

.container-fluid {
    padding: 0 160px;
    @include laptopb {
        padding: 0 150px;
    }
    @include tabletb {
        padding: 0 80px;
    }
    @include mphoneb {
        padding: 0 20px;
    }
    @include phoneboo {
        padding: 0 10px;
    }
}

body {
    font-size: 14px;
    min-width: 320px;
    line-height: 1.42857143;
    font-family: 'Open Sans', sans-serif;
    overflow-x: hidden;
    opacity: 1;
    font-smoothing: antialiased;
}

.wrapper {
    position: relative;
    overflow: hidden;
    &:after {
        content: '';
        display: block;
        height: $footer-height;
    }
    &_s {
        &:after {
            height: $footer-heights;
        }
    }
    &_sm {
         &:after {
            height: $footer_sm;
        }
    }
}

.footer {
    height: $footer-height;
    margin-top: -$footer-height;
    &_s {
        height: $footer-heights;
        margin-top: -$footer-heights;
    }
    &_sm {
        height: $footer_sm;
        margin-top: -$footer_sm;
    }
}

address {
    margin: 0;
}

ul {
    padding: 0;
    margin: 0;
}

li {
    list-style: none;
}

a {
    color: $black;
    cursor: pointer;
    &:hover,
    &:focus {
        outline: none;
        text-decoration: none;
        color: $black;
        cursor: pointer;
    }
}

p {
    margin: 0;
    font-family: $rom;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    font-family: $rom;
}

i,
cite,
em,
var,
address,
dfn {
    font-style: normal;
}

dl,
dd {
    margin: 0;
}

* {
    box-sizing: border-box;
}

*::-webkit-input-placeholder {
    color: $black!important;
    opacity: 1;
    text-transform: uppercase;
    font-size: 11px!important;
}

*:-moz-placeholder {
    color: $black!important;
    opacity: 1;
    text-transform: uppercase;
    font-size: 11px!important;
}

*::-moz-placeholder {
    color: $black!important;
    opacity: 1;
    text-transform: uppercase;
    font-size: 11px!important;
}

*:-ms-input-placeholder {
    color: $black!important;
    opacity: 1;
    text-transform: uppercase;
    font-size: 11px!important;
}

body input:focus:required:invalid,
body textarea:focus:required:invalid {
    color: #666;
}

body input:required:valid,
body textarea:required:valid {
    color: #666;
}

// Карта, забрати автоматичний тач
.map-wrap {
    position: relative;
}

.overlay-map {
    position: absolute;
    width: 100%;
    top: 0;
    min-height: 476px;
}

