.sec-header {
    &__logo {
        max-height: 140px;
        max-width: 140px;
        margin: 0 auto;
    }
    &__wrap {
        margin-top: 10px;
    }
    @include tabletm {
        margin-bottom: 70px;
    }
}

.enter {
    font-weight: bold;
    @include phonem {
        text-align: center;
    }
    &__title {
        font-size: 35px;
        margin-top: 40px;
        text-transform: uppercase;
        text-align: center;
        font-weight: bold;
        color: $black;
        margin-bottom: 20px;
        @include laptopm {
            font-size: 35px;
        }
        @include phonem {
            font-size: 25px;
        }
        &:after,
        &:before {
            font-family: FontAwesome;
            content: "\f111";
            font-size: 8px;
            color: #ed801c;
            display: inline-block;
            vertical-align: middle;
        }
        &:after {
            margin-left: 30px;
        }
        &:before {
            margin-right: 30px;
        }
    }
}

.restoran {
    position: relative;
    text-align: center;
    max-width: 555px;
    max-height: 500px;
    margin: 0 auto;
    &__simg {
        position: absolute;
        transition: all .3s ease-out;
    }
    &__img {
        max-width: 90%;
        max-height: 100%;
        transition: all .3s ease-out;
    }
    &__link {
        display: block;
        width: 100%;
        height: 100%;
        cursor: pointer;
    }
    &__img-s {
        max-width: 80px;
        max-height: 80px;
    }
    &__simg_1 {
        top: -5%;
        left: 41%;
    }
    &__simg_2 {
        top: 1%;
        left: 18%;
    }
    &__simg_3 {
        top: 21%;
        left: 2%;
    }
    &__simg_4 {
        top: 44%;
        left: -1%;
    }
    &__simg_5 {
        top: 68%;
        left: 3%;
    }
    &__simg_6 {
        top: 83%;
        left: 17%;
    }
    &__simg_7 {
        top: 88%;
        left: 39%;
    }
}

.pom {
    &__wrap {
        position: relative;
        text-align: center;
        max-width: 555px;
        max-height: 500px;
        margin: 0 auto;
    }
    &__link {
        display: block;
        cursor: pointer;
    }
    &__img {
        position: absolute;
        top: 0;
        right: 10%;
    }
}

.scale {
    transform: scale(1.1);
    @include phonem {
        transform: scale(1);
    }
}

.single-scale {
    transform: scale(1.3);
    @include phonem {
        transform: scale(1);
    }
}

.mobileUI {
    &__item {
        background: url(../img/mrs4.png) center center no-repeat;
        background-size: cover;
        min-height: 300px;
        position: relative;
        &_acco {
            display: none;
        }
        @include phoneboo {
            min-height: 250px;
        }
    }
    &__ico {
        color: $white;
        font-size: 30px;
        position: absolute;
        right: 50px;
        top: 7px;
        @include phoneboo {
            right: 10px;
            top: 1px;
            font-size: 25px;
        }
    }
    &__center {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
    }
    & .enter__title {
        margin: 0;
        color: $white;
        font-size: 40px;
        font-weight: 300;
        @include phoneboo {
            font-size: 25px;
        }
    }
}

.restowrap {
    display: none;
    position: relative;
    top: -90px;
}
