.mlyn {
    &__title {
        font-size: 30px;
        text-transform: uppercase;
        font-weight: bold;
        color: $black;
        margin-top: 90px;
        text-align: center;
    }
    &__column {
        padding: 0;
    }
}

.gosti {
    & .plus-img__wrap {
        min-height: auto;
        margin-bottom: 25px;
    }
    & .plus__item {
        margin-top: 50px;
    }
    & .plus__img {
        max-width: 100%;
        max-height: 100%;
    }
    &_menu {
        & .mlyn__title {
            color: $burgundy;
        }
    }
    & .plus__txt p {
        margin-bottom: 0;
    }
}

.bgslider {
    & .owl-nav.disabled+.owl-dots {
        margin-top: 25px;
    }
    & .owl-dots .owl-dot span {
        background: #ad6f41;
    }
    & .owl-dots .owl-dot.active span,
    & .owl-dots .owl-dot:hover span {
        background: #7b5536;
    }
}

.decor {
    position: relative;
    display: block;
    @extend %clearfix;
    width: 266px;
    min-height: 144px;
    margin: 0 auto;
    &:hover {
        & .decor__span {
            background-color: #a93828;
        }
    }
    &__span {
        position: absolute;
        display: block;
        z-index: 2;
        top: 51px;
        left: 61px;
        padding: 10px 26px 9px 26px;
        letter-spacing: 1px;
        font-size: 16px;
        text-transform: uppercase;
        color: $white;
        background-color: #7f2215;
        font-weight: bold;
    }
    &__img {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 3;
    }
}

.myres {
    margin-top: 110px;
    text-align: center;
    &__video {
        margin-top: 115px;
        & .myres__title {
            margin-bottom: 10px;
        }
    }
    &__txt {
        margin-top: 110px;
        margin-bottom: 95px;
        & p {
            color: $lg;
            font-size: 15px;
            width: 80%;
            margin: 0 auto;
        }
    }
    &__img {
        margin-bottom: 50px;
        margin: 0 auto;
    }
    &__title {
        font-size: 35px;
        text-transform: uppercase;
        font-weight: bold;
        &_sm {
            font-size: 20px;
            margin-bottom: 30px;
        }
    }
    &slider {
        &__img {
            width: 289px;
            height: 175px;
        }
    }
}

.player {
    position: relative;
    min-height: 527px;
    &__border {
        position: absolute;
        z-index: 2;
        left: 50%;
        transform: translateX(-50%);
    }
    &__video {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 3;
    }
}
