        .brownlist {
            &_menu {
                text-align: center;
                & .brownlist__item {
                    font-weight: bold;
                    margin-right: 10px;
                }
                & .brownlist__link:focus {
                    border: 1px solid $brown;
                    padding: 10px;
                    display: block;
                    &:before {
                        content: none;
                    }
                    & + li {
                        &:before {
                            content: none;
                        }
                    }
                }
                // & .brownlist__item_active {
                //     display: block;
                //     width: 14%;
                //     margin: 10px auto;
                //     & .brownlist__link {
                //         border: 1px solid $brown;
                //         padding: 10px;
                //         display: block;
                //     }
                //     &:before {
                //         content: none;
                //     }
                //     & + li {
                //         &:before {
                //             content: none;
                //         }
                //     }
                // }
            }
            &__item {
                display: inline-block;
                // margin-right: 10px;
                &_active {
                    & .brownlist__link {
                        font-weight: bold;
                    }
                }
                &:first-child {
                    &:before {
                        content: none;
                    }
                }
                &:last-child {
                    margin-right: 0;
                }
                &:before {
                    content: attr(data-content) '/';
                    color: $brown;
                    font-size: 15px;
                    font-weight: bold;
                    margin: 0 5px;
                }
                &.changebefore:before {
                    content: attr(data-content);
                    width: 0;
                    height: 0;
                }
            }
            &__link {
                font-size: 14px;
                color: $brown;
                text-transform: uppercase;
            }
        }
        
        .oldmlyn {
            &__top {
                margin-bottom: 120px;
            }
        }
        
        .bars {
            &__item {
                color: $brown;
                background: $white;
                padding: 30px 15px;
                margin-bottom: 30px;
                width: 80%;
                margin-left: 10%;
            }
            &__link {}
            &__img {
                margin: 0 auto;
            }
            &__list {
                padding-left: 38px;
            }
            &__il {
                list-style: disc;
            }
            &__span {
                font-weight: bold;
                text-transform: uppercase;
                display: block;
                margin-top: 13px;
            }
            &__btn {
                text-transform: uppercase;
                padding: 15px 30px;
                background: $white;
                border: 1px solid $brown;
                display: block;
                text-align: center;
                color: $brown;
                font-weight: bold;
                margin-top: 20px;
                margin-left: 20px;
                margin-right: 20px;
            }
        }
        