.footer {
    &__logo,
    &__social,
    &__bottom {
        text-align: center;
    }
    &__img {
        max-height: 150px;
        margin-top: 30px;
    }
    &__social {
        margin-top: 30px;
    }
    &__bottom {
        text-align: center;
        margin: 30px 0;
    }
    
}

.bottom-menu {
    &__item {
        display: inline-block;
        padding: 10px 10px 10px 0;
        text-transform: uppercase;
        font-weight: bold;
    }
    &__link:before {
        font-family: FontAwesome;
        content: "\f111";
        font-size: 8px;
        color: $orange;
        display: inline-block;
        vertical-align: middle;
        margin-right: 10px;
    }
    &_del {
        & .bottom-menu {
            &__item {
                font-size: 17px;
                &:first-child {
                    & .bottom-menu__link:before {
                        content: '';
                    }
                }
            }
        }
    }
}

.social {
    padding: 0;
    &__item {
        display: inline-block;
        border-radius: 50%;
        background-color: $grey;
        color: $white;
        width: 40px;
        height: 40px;
        margin: 10px 10px 0 0;
    }
    &__link {
        display: block;
        width: 100%;
        height: 100%;
    }
    &__icon {
        color: $white;
        font-size: 20px;
        padding-top: 10px;
    }
}

.spy {
    text-align: center;
    &__link {
        &_bot {
            display: block;
            text-transform: uppercase;
            margin: 10px;
            font-weight: bold;
        }
    }
}
