.delivery {
    &__img {
        display: inline-block;
        vertical-align: middle;
        margin-right: 20px;
    }
    &__title {
        text-align: center;
        font-size: 30px;
        text-transform: uppercase;
        font-weight: bold;
        margin-bottom: 10px;
        &_top {
            display: inline-block;
            vertical-align: middle;
        }
        &_mid {
        	margin-top: 60px;
        	margin-bottom: 50px;
        }
        &_bot {
        	margin-bottom: 50px;
        }
            }
    &__menu {
        text-align: center;
    }
    &__top,
    &__mid,
    &__bot {
        margin-top: 60px;
    }
    & .plus {
        &__img {
            max-width: 240px;
            min-height: 240px;
        }
        &__item {
            margin-bottom: 100px;
        }
        &__btn {
            @include desktopb {
                font-size: 12px !important;
                width: 120px !important;
            }
        }
    }
    
}
