.check {
    &__title {
        text-align: left;
        font-size: 14px;
        font-weight: bold;
        margin: 60px 0 50px;
        @include phonem {
            text-align: center;
        }
    }
    &__middle {
        margin-bottom: 130px;
        & .plus-img__wrap {
            min-height: 0;
            &:after {
                transform: scale(0.9);
                right: 4px;
                top: -19px;
            }
        }
        & .plus__item {
            @include laptopb {
                margin-bottom: 50px;
            }
        }
    }
    &__bot {
        margin-bottom: 230px;
        & .container-fluid {
            @include phoneboo {
                padding: 0 25px;
            }
        }
        & .check__title {
            margin: 0;
        }
        &-title {
            text-align: center!important;
            text-transform: uppercase;
            font-size: 14px;
            font-weight: bold;
            margin-top: 15px;
            @include tabletm {
                margin-bottom: 20px;
                margin-top: 0;
            }
        }
        &-del {
            margin-bottom: 60px;
        }
        &-sam {
            margin-top: 60px;
            & .icon-addon.addon-lg .form-control {
                font-size: 9px;
            }
        }
    }
}

.center-block {
    float: none;
    margin-left: auto;
    margin-right: auto;
}

.input-group .icon-addon .form-control {
    border-radius: 0;
}

.icon-addon {
    position: relative;
    color: #555;
    display: block;
    margin-bottom: 30px;
}

.icon-addon:after,
.icon-addon:before {
    display: table;
    content: " ";
}

.icon-addon:after {
    clear: both;
}

.icon-addon.addon-md .glyphicon,
.icon-addon .glyphicon,
.icon-addon.addon-md .fa,
.icon-addon .fa {
    position: absolute;
    z-index: 2;
    font-size: 14px;
    width: 20px;
    margin-left: -2.5px;
    text-align: center;
    padding: 10px 0;
    top: 1px
}

.icon-addon.addon-lg .form-control {
    line-height: 1.33;
    height: 42px;
    font-size: 14px;
    // text-transform: uppercase;
    padding: 10px 46px 10px 40px;
    font-weight: bold;
}

.icon-addon.addon-sm .form-control {
    height: 30px;
    padding: 5px 10px 5px 28px;
    font-size: 12px;
    line-height: 1.5;
}

.icon-addon.addon-lg .fa,
.icon-addon.addon-lg .glyphicon {
    font-size: 18px;
    margin-left: 0;
    top: 4px;
    right: 20px!important;
    margin-bottom: 0;
    color: $black;
    &:hover {
        color: $black;
    }
}

.icon-addon.addon-md .form-control,
.icon-addon .form-control {
    padding-left: 30px;
    float: left;
    font-weight: normal;
}

.icon-addon.addon-sm .fa,
.icon-addon.addon-sm .glyphicon {
    margin-left: 0;
    font-size: 12px;
    left: 5px;
    top: -1px
}

.icon-addon .form-control:focus + .glyphicon,
.icon-addon:hover .glyphicon,
.icon-addon .form-control:focus + .fa,
.icon-addon:hover .fa {
    color: $black;
}

.greybl {
    min-height: 110px;
    background-color: $chgrey;
    border-radius: 55px;
    margin-bottom: 10px;
    &_items {
        & .greybl__item {
            padding: 5px 0!important;
        }
    }
    &__adr {
        padding-top: 35px;
        text-align: right;
        @include laptopm {
            text-align: center;
        }
        @include tabletb {
            padding-top: 18px;
        }
        @include phonem {
            text-align: center;
            padding-top: 10px;
            padding-bottom: 10px;
        }
    }
    &__address {
        font-size: 11px;
        color: $cabtgrey;
        display: inline-block;
        vertical-align: middle;
        margin-right: 10px;
        @include laptopm {
            width: 100%;
        }
        @include tabletb {
            margin-right: 51px;
            margin-bottom: 3px;
        }
    }
    &_cab {
        padding-left: 40px;
        padding-right: 10px;
        position: relative;
        & .mainbutt__btn {
            display: inline-block;
            vertical-align: middle;
        }
        & .greybl__imgtitle {
            font-size: 10px;
        }
        & .greybl__item {
            padding-top: 32px;
            padding-bottom: 33px;
            padding-left: 0;
            margin-right: 20px;
            @include phonem {
                padding: 10px 0;
            }
        }
        & .greybl__link {
            padding: 0;
            padding-top: 3px;
            font-size: 14px;
            text-transform: lowercase;
            font-weight: bold;
            text-align: right;
            margin-right: 100px;
            @include laptopm {
                text-align: center;
            }
            @include tabletb {
                text-align: left;
                margin-left: 103px;
            }
            @include phonem {
                text-align: center;
                margin: 0;
            }
        }
        & .mainbutt_sm .mainbutt__btn {
            width: 180px;
        }
        & .mainbutt_sm .plus__btn {
            @include desktopb {
                font-size: 11px!important;
                width: 180px!important;
            }
            @include laptopb {
                width: 180px!important;
            }
        }
        & .mainbutt__i {
            font-size: 13px;
            color: $lg;
        }
        & .mainbutt_sm .mainbutt__i .price {
            font-size: 14px;
        }
        & .mainbutt__list {
            padding-top: 4px;
            position: relative;
            z-index: 2;
            margin-left: 1px;
        }
        & .mainbutt__btn:after {
            background-color: $chgrey;
        }
        & .mainbutt__btn:hover:after {
            height: 38px;
        }
    }
    &__piza {
        @include laptopm {
            text-align: center;
        }
    }
    &_sm {
        margin-bottom: 40px;
        width: 69%;
        margin-left: 3%;
        min-height: 77px;
        padding-top: 16px;
        & .row {
            margin: 0;
            width: 100%;
        }
        & .greybl {
            &__left {
                padding-left: 50px;
            }
            &__imgtitle {
                font-size: 10px;
            }
            &__item {
                width: 16%;
                padding: 18px 0;
                &_p {
                    padding-left: 15px;
                }
            }
            &__img {
                margin-left: 0 5px;
            }
            &__count {
                width: 77px;
                margin-left: 5px;
            }
            &__price {
                width: 100%;
                padding-top: 9px;
            }
        }
        & .btn {
            &-down,
            &-up {
                font-size: 13px;
                padding: 2px 7px;
            }
        }
        & .number {
            height: 30px;
            font-size: 14px;
        }
    }
    &__is {
        padding-right: 0;
        @include laptopb {
            padding-right: 5px;
        }
        @include tabletm {
            text-align: center;
        }
    }
    &__ib {
        padding: 0;
        @include laptopb {
            padding-right: 5px;
        }
        @include tabletm {
            text-align: center;
        }
    }
    &__piza {
        @include tabletm {
            text-align: center;
        }
    }
    &__dish {
        @include tabletm {
            text-align: center;
        }
    }
    &__item {
        display: inline-block;
        vertical-align: middle;
        padding-top: 18px;
        padding-bottom: 18px;
        padding-left: 45px;
        overflow: hidden;
        @include desktopm {
            padding-top: 10px;
            padding-bottom: 10px;
        }
    }
    &__imgtitle,
    &__img {
        display: inline-block;
        vertical-align: middle;
    }
    &__img {
        margin-right: 10px;
    }
    &__imgtitle {
        font-size: 15px;
        text-transform: uppercase;
        font-weight: bold;
    }
    &__price {
        display: inline-block;
        vertical-align: middle;
        font-size: 17px;
        font-weight: bold;
        color: $lg;
        text-align: right;
        @include tabletm {
            text-align: center;
        }
    }
    &__count {
        width: 106px;
        display: inline-block;
        vertical-align: middle;
    }
    &__link {
        color: $red;
        text-transform: uppercase;
        font-size: 13px;
        font-weight: bold;
        display: block;
        padding: 45px 0;
        text-align: center;
        @include desktopm {
            padding: 10px 0;
        }
    }
    &__right {
        text-align: right;
        padding-right: 70px;
    }
}

.count {
    & .form-group {
        margin: 0;
    }
    &__wrap {
        @extend %clearfix;
        padding: 35px 0;
        text-align: right;
        @include desktopm {
            padding: 27px 0;
        }
        @include phonem {
            text-align: center;
        }
        & .greybl__count {
            float: left;
            @include phonem {
                float: none;
                padding-top: 1px;
                margin-right: 20px;
            }
        }
        & .greybl__price {
            float: right;
            padding-top: 6px;
            @include phonem {
                float: none;
                padding-top: 0px;
            }
        }
    }
}

.btn- {
    &up,
    &down {
        border-radius: 50%;
        border: none;
        background-image: none;
        padding: 2px 10px;
        font-size: 15px;
        font-weight: bold;
        outline: none;
    }
}

.number {
    border-radius: 50%!important;
    border: none;
    height: 40px;
    font-size: 17px;
    font-weight: bold;
    padding-right: 8px;
    padding-left: 7px;
}

.mainform {
    &__input {
        border: 1px solid #ff6600;
        border-radius: 20px;
        height: 42px;
        &:focus {
            border-color: #ff6600;
            box-shadow: inset 0 1px 1px rgba(237, 121, 38, .075), 0 0 8px rgba(218, 44, 40, .6);
        }
        &_sub {
            background-image: $bg;
            color: $white;
            font-size: 15px;
            font-weight: bold;
        }
    }
    &__control-label {
        padding-top: 11px;
    }
    &__txt {
        text-align: right;
        padding-top: 9px;
        @include tabletm {
            text-align: center;
        }
    }
}

.nav-tabs {
    border-bottom: none;
    &>li>a {
        border-radius: 20px;
        border: 1px solid #ff6600;
        margin-right: 30px;
        margin-bottom: 10px;
        &:last-child {
            margin-bottom: 0;
        }
        &:hover {
            color: $white;
            background-image: $bg;
        }
    }
    &__icon {
        margin-left: 5px;
    }
}

.nav-tabs>li.active>a,
.nav-tabs>li.active>a:focus,
.nav-tabs>li.active>a:hover {
    border: none;
    color: $white;
    background-image: $bg;
    & i {
        display: none;
    }
}

.mainform {
    margin-top: 60px;
}

.ingr {
    &_bl {
        padding: 0 38px;
        position: relative;
        margin-top: 40px;
        & img {
            width: 100%;
            opacity: 0.5;
        }
        & .count {
            width: 106px;
            display: inline-block;
            vertical-align: middle;
        }
    }
    &_txt {
        color: #363637;
        text-transform: uppercase;
        text-align: center;
        margin-top: 15px;
        & span {
            text-transform: none;
        }
    }
}

.add_bl {
    position: absolute;
    top: 0;
    left: 0;
    & span {
        display: none;
        width: 30px;
        line-height: 30px;
        text-align: center;
        background: #bc1521;
        font-size: 18px;
        font-weight: 400;
        color: #ffffff;
        margin: 10px 0;
    }
}

.plus_ingr {
    background: url(../img/sprite.png) 8px -4780px no-repeat;
    display: block;
    width: 30px;
    height: 30px;
    border: 1px solid transparent;
    &:hover {
        border: 1px solid #3e3e40;
    }
}

.minus_ingr {
    display: none;
    background: url(../img/sprite.png) 8px -4743px no-repeat;
    width: 30px;
    height: 30px;
    border: 1px solid transparent;
    &:hover {
        border: 1px solid #3e3e40;
    }
}

.name_ingr {
    line-height: 18px;
    height: 36px;
    padding: 0 5px;
}

.price_ingr {
    font-size: 18px;
    font-weight: 600;
    margin-top: 5px;
}
.dodatok {
    margin-bottom: 30px;
    display: none;
}