    .cabinet {
        font-weight: bold;
        &__title {
            font-size: 14px;
            font-weight: bold;
            text-transform: uppercase;
            margin-bottom: 40px;
            @include phonem {
                text-align: center;
            }
            &:after {
                font-family: FontAwesome;
                content: "\f111";
                font-size: 8px;
                color: #ed801c;
                display: inline-block;
                vertical-align: middle;
                margin-left: 30px
            }
        }
    }
    
    .proces {
        margin-bottom: 100px;
        @include phonem {
            margin-bottom: 20px;
        }
    }
    
    .circle {
        width: 38px;
        height: 38px;
        background-color: $cabi;
        border-radius: 50%;
        position: relative;
        margin: 0 auto;
        &_sm {
            width: 22px;
            height: 22px;
            margin: 0;
            float: right;
            @include tabletm {
                float: none;
                margin: 0 auto;
            }
            & .how-to__num {
                font-size: 12px;
                top: 2px;
                left: 8px;
            }
        }
    }
    
    .how-to {
        &__item {
            position: relative;
            @include phonem {
                margin-bottom: 20px;
                &:last-child {
                    margin-bottom: 0;
                }
            }
            &:first-child {
                &:before {
                    content: '';
                    height: 0;
                    width: 0;
                }
            }
            &:hover {
                & .how-to__wrap {
                    background: url(../img/earc.png) no-repeat;
                }
                & .how-to__txt {
                    color: $cabi;
                }
            }
            &:before {
                position: absolute;
                top: 26%;
                left: -10%;
                content: '';
                height: 3px;
                width: 60px;
                background-color: $cabi;
                @include laptopb {
                    left: -7%;
                    width: 30px;
                }
                @include phonem {
                    height: 0;
                    width: 0;
                }
            }
            &_b {
                & .how-to__wrap {
                    width: 102px;
                    height: 134px;
                    background: url(../img/bot.png) no-repeat;
                }
                &:hover {
                    & .how-to__wrap {
                        background: url(../img/boty.png) no-repeat;
                    }
                }
            }
            &_m {
                &:before {
                    left: -17%;
                }
                & .how-to__wrap {
                    width: 157px;
                    height: 134px;
                    background: url(../img/moto.png) no-repeat;
                }
                &:hover {
                    & .how-to__wrap {
                        background: url(../img/motoy.png) no-repeat;
                    }
                }
            }
            // &:first-child {
            //     &:before {
            //     }
            // }
        }
        &__wrap {
            background: url(../img/ear.png) no-repeat;
            width: 131px;
            height: 134px;
            margin: 0 auto;
        }
        &__txt {
            text-align: center;
            font-weight: bold;
            margin: 30px 0;
        }
        &__num {
            color: $white;
            position: absolute;
            font-size: 18px;
            font-weight: bold;
            top: 5px;
            left: 14px;
        }
    }
    
    .lovest {
        & .cabinet__title {
            margin-bottom: 5px;
            margin-top: 3px;
        }
        &__txt {
            font-size: 10px;
            text-transform: uppercase;
            margin-bottom: 40px;
            @include phonem {
                text-align: center;
            }
        }
        &__time {
            font-size: 14px;
        }
        &__bottom {
            margin-top: 40px;
            margin-bottom: 40px;
        }
    }
    
    .times {
        @include phonem {
            text-align: center;
        }
    }
    
    .adrdel {
        margin-top: 80px;
        & .cabinet__title {
            margin-bottom: 80px;
            &:after {
                content: '';
            }
        }
        & .greybl_cab {
            min-height: 80px;
            padding-top: 29px;
            margin-bottom: 20px;
            &:last-child {
                margin-bottom: 35px;
            }
            & p {
                font-size: 13px;
            }
        }
        &__list {
            font-size: 11px;
            @include tabletm {
                text-align: center;
            }
        }
        &__item {
            display: inline-block;
            @include phonem {
                text-align: left;
            }
            &:first-child {
                &:before {
                    color: '';
                }
            }
            &:before {
                font-family: FontAwesome;
                content: "\f111";
                font-size: 8px;
                color: #ed801c;
                display: inline-block;
                vertical-align: middle;
                margin-left: 10px;
                margin-right: 10px;
            }
            @include phonem {
                display: block;
                width: 50%;
                margin: 0 auto;
            }
        }
    }
    
    .tel {
        @include phonem {
            display: block;
            width: 50%;
            margin: 0 auto;
        }
        &__item {
            display: inline-block;
            font-size: 11px;
            margin-right: 34px;
            &:last-child {
                margin-right: 0;
            }
            @include phonem {
                text-align: left;
            }
        }
        & .greybl__link {
            font-size: 12px;
            margin: 0;
        }
    }
    
    .owndata {
    	margin-top: 160px;
    	& .cabinet__title {
    		padding-top: 15px;
    	}
    }
