.main {
    &__l,
    &__r {
        margin-top: 50px;
    }
    &__r {
        @include tabletm {
            margin-top: 100px;
        }
        @include phoneboo {
            margin-top: 50px;
        }
    }
    &__title {
        font-size: 22px;
        font-weight: bold;
        margin-bottom: 10px;
    }
}

.dish {
    margin-bottom: 40px;
    &__txt {
        font-size: 14px;
        margin-bottom: 30px;
        color: $lg;
    }
}

.mainbutt {
    &_check {
        &-noneimage {
            & .mainbutt__btn {
                &:after {
                    background-image: none!important;
                }
            }
        }
        padding: 36px 0;
        @include desktopm {
            padding: 28px 0;
        }
        @include tabletm {
            padding: 5px 0;
        }
        & .mainbutt__btn {
            font-size: 13px;
            padding: 8px;
            padding-left: 20px;
            width: 225px;
            position: relative;
            &:hover {
                &:after {
                    width: 48px;
                    height: 48px;
                    right: -22px;
                    top: -8px;
                    border: none;
                }
            }
            &:after {
                content: '';
                position: absolute;
                right: -22px;
                top: -8px;
                height: 48px;
                width: 48px;
                background-color: transparent;
                border: none;
                background-image: url(../img/check.png);
            }
        }
    }
    &_sm {
        & .mainbutt__btn {
            font-size: 11px;
            padding: 6px;
            width: 100px;
            &:hover {
                &:after {
                    width: 16px;
                    height: 39px;
                    right: -8px;
                    top: -5px;
                }
            }
            @include laptopb {
                width: 95px;
            }
            &:after {
                width: 16px;
                height: 36px;
                right: -9px;
                top: -5px;
            }
        }
        & .mainbutt__list {
            margin-left: 11px;
            padding-top: 2px;
        }
        & .mainbutt__i {
            font-size: 11px;
            & .price {
                font-size: 12px;
            }
        }
    }
    &__btn {
        font-size: 16px;
        font-weight: bold;
        color: $white;
        padding: 12px;
        padding-left: 20px;
        width: 180px;
        text-transform: uppercase;
        background-image: $bg;
        border-radius: 20px;
        border: 2px solid transparent;
        transition: all 0.3s ease 0s;
        &:hover {
            background-image: none;
            border: 2px solid #da2c28;
            background-color: $white;
            &:after {
                border-left: 2px solid #da2c28;
                height: 60px;
                right: -11px;
                top: -7px;
            }
        }
        &:after {
            content: '';
            width: 21px;
            height: 60px;
            background-color: $white;
            position: absolute;
            right: -11px;
            top: -7px;
            border-radius: 50%;
            border: 2px solid transparent;
        }
    }
    &__list {
        display: inline-block;
        margin-left: 16px;
        padding-top: 10px;
        font-weight: bold;
    }
    &__i {
        display: inline-block;
        font-size: 15px;
        @include phoneboo {
            font-size: 13px;
        }
        & .price {
            font-size: 20px;
            @include phoneboo {
                font-size: 17px;
            }
        }
    }
}

.add {
    margin-bottom: 20px;
    &_s {
        margin-bottom: 15px;
        & .add__i {
            font-size: 10px;
            margin-right: 0;
        }
    }
    &__i {
        display: inline-block;
        font-size: 14px;
        color: $lg;
        margin-right: 4px;
        &_margin {
            position: relative;
            top: -5px;
        }
    }
}

.plus {
    margin-bottom: 90px;
    position: relative;
    &-img__wrap {
        display: block;
        cursor: pointer;
        position: relative;
        max-width: 240px;
        min-height: 240px;
        margin: 0 auto;
        transition: all .3s ease-out;
        &:after {
            content: '';
            position: absolute;
            right: -10px;
            top: -15px;
            height: 79px;
            width: 79px;
            background-image: url(../img/del.png);
        }
        &_none {
            &:after {
                width: 0;
                height: 0;
                position: static;
                background-image: none;
            }
        }
    }
    &__price {
        font-size: 16px;
        color: $lg;
        text-align: center;
        font-weight: bold;
        margin-top: 18px;
    }
    &__btn {
        @include desktopb {
            font-size: 10px!important;
            width: 86px!important;
        }
    }
    &_dod {
        & .main__title {
            font-size: 30px;
            font-weight: 700;
            margin-bottom: 10px;
        }
    }
    &__item {
        text-align: center;
        @include tabletm {
            margin-top: 80px;
        }
    }
    &__title {
        margin-bottom: 25px;
        text-transform: uppercase;
        & .main__title {
            font-weight: 500;
        }
    }
    &__img {
        max-width: 140px;
        max-height: 140px;
        margin: 0 auto;
        margin-bottom: 15px;
    }
    &__smtitle {
        font-size: 15px;
        font-weight: bold;
        text-transform: uppercase;
    }
    &__txt p {
        font-size: 10px;
        color: $lg;
        margin-bottom: 25px;
        margin-top: 5px;
    }
}

.preview {
    position: relative;
    margin: 0 auto;
    max-height: 360px;
    max-width: 360px;
    @include phoneboo {
        margin: 0;
        max-width: 250px;
        max-height: 250px;
    }
    &:after {
        content: '';
        position: absolute;
        right: -2px;
        top: -14px;
        height: 79px;
        width: 79px;
        background-image: url("../img/del.png");
    }
    &__img {
        max-width: 330px;
        max-height: 330px;
        @include tablet {
            height: 330px!important;
        }
    }
    &__block1,
    &__block2 {
        position: absolute;
    }
    &__block1 {
        right: -49px;
        bottom: 55px;
    }
    &__block2 {
        right: -3px;
        bottom: -8px;
    }
}

.bredwrap {
    margin-top: 40px;
    text-align: center;
    & .breadcrumb {
        font-size: 13px;
        margin: 0;
        background: transparent;
        & > .active {
            color: $dgrey;
        }
    }
    &_del {
        margin-bottom: 40px;
    }
}

.controls {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    &__btn {
        &_prev {
            left: -20px;
            @include phoneb {
                left: 5px;
            }
        }
        &_next {
            right: -20px;
            @include phoneb {
                right: 5px;
            }
        }
    }
    &__link {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 10;
        font-size: 45px;
        color: $dgrey;
        @include phoneb {
            font-size: 60px;
        }
    }
}

.secslider {
    &__btn {
        &_prev {
            left: 43px;
            @include laptopb {
                left: 15px;
            }
            @include tabletb {
                left: 15px;
            }
            @include phoneb {
                left: 20px;
            }
        }
        &_next {
            right: 43px;
            @include laptopb {
                right: 15px;
            }
            @include tabletb {
                right: 15px;
            }
            @include phoneb {
                right: 20px;
            }
        }
    }
}

.carousel-fade .carousel-inner .item {
    -webkit-transition-property: opacity;
    transition-property: opacity;
}

.carousel-fade .carousel-inner .item,
.carousel-fade .carousel-inner .active.left,
.carousel-fade .carousel-inner .active.right {
    opacity: 0;
}

.carousel-fade .carousel-inner .active,
.carousel-fade .carousel-inner .next.left,
.carousel-fade .carousel-inner .prev.right {
    opacity: 1;
}

.carousel-fade .carousel-inner .next,
.carousel-fade .carousel-inner .prev,
.carousel-fade .carousel-inner .active.left,
.carousel-fade .carousel-inner .active.right {
    left: 0;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.carousel-fade .carousel-control {
    z-index: 2;
}

html,
body,
.carousel,
.carousel-inner,
.carousel-inner .item {
    height: 100%;
}

