.header {
    font-family: $rom;
    &__top {
        min-height: 90px;
        background-color: $white;
        position: fixed;
        width: 100%;
        top: 0;
        left: 0;
        z-index: 5;
        @include laptopb {
            min-height: 105px;
        }
    }
    &__left {
        padding-top: 30px;
    }
    &__right {
        @extend %clearfix;
    }
    &__bottom {
        height: 60px;
        background-color: #ffb53d;
        z-index: 4;
        position: relative;
    }
    &_contacts {
        padding-top: 40px;
        & .header__top {
            @include laptopm {
                text-align: center;
            }
        }
        & .header__left {
            padding: 0;
        }
        & .brownlist {
            padding-top: 41px;
        }
        & .logo__img {
            margin: 0 auto;
        }
        & .topmenu,
        & .basket {
            width: auto;
            float: none;
        }
        & .basket {
            margin-left: 0;
            &__text {
                & p {
                    font-size: 12px;
                }
            }
        }
        & .topmenu {
            text-align: left;
            @include tabletm {
                text-align: center;
            }
        }
    }
}

.topmenu {
    padding-top: 30px;
    width: 70%;
    float: left;
    text-align: right;
    @include phonem {
        width: 100%;
        text-align: center;
    }
    @include laptopb {
        width: 77%;
    }
    &__item {
        display: inline-block;
        margin-right: 5px;
        font-size: 13px;
        letter-spacing: 1px;
        font-weight: bold;
        font-family: 'Roboto', sans-serif;
        &:last-child {
            margin-right: 0;
        }
        &:before {
            content: '';
            transform: scale(0.8);
            vertical-align: middle;
            display: inline-block;
            background-image: url('../img/spritesheet.png');
            background-repeat: no-repeat;
            margin-right: 5px;
            margin-bottom: 2px;
        }
        &_go {
            &:before {
                height: 37px;
                background-position: -38px -2px;
                width: 44px;
            }
        }
        &_numb {
            font-size: 13px;
            &:before {
                height: 32px;
                background-position: -2px -2px;
                width: 32px;
            }
        }
        &_enter {
            font-size: 13px;
            &:before {
                height: 32px;
                background-position: -2px -43px;
                width: 32px;
            }
        }
    }
}

.basket {
    position: relative;
    height: 90px;
    margin-left: 2%;
    width: 28%;
    float: right;
    border-left: 3px solid #fafafa;
    border-right: 3px solid #fafafa;
    text-align: center;
    @include laptopb {
        width: 23%;
    }
    &__box {
        transform: translate(-50%, -50%);
        position: absolute;
        left: 50%;
        top: 50%;
    }
    &__img {
        width: 80%;
    }
    &__num {
        position: absolute;
        top: 4px;
        right: 9px;
        font-size: 15px;
        color: $white;
    }
    &__circle {
        height: 27px;
        width: 27px;
        background-image: $bg;
        border-radius: 50%;
        position: absolute;
        top: -11px;
        right: -13px;
    }
    @include laptopm {
        margin-left: 0;
        border: 0;
    }
    @include phonem {
        width: 100%;
    }
    &__text {
        padding-top: 66px;
        @include phonem {
            text-align: center;
        }
        & p {
            font-weight: bold;
            padding-left: 13px;
            font-size: 12px;
        }
    }
}

.logo {
    @include laptopm {
        text-align: center;
    }
}

.restore {
    padding-top: 7px;
    display: inline-block!important;
    vertical-align: middle;
    width: 82%!important;
    @include laptopb {
        width: 81%!important;
    }
    @include tabletb {
        width: 79%!important;
    }
    @include mphoneb {
        width: 69%!important;
    }
    &__controls {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }
    &__btn {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 10;
        font-size: 35px;
        color: $white;
        &:hover,
        &:active,
        &:focus {
            color: $white;
        }
        &_next {
            right: 5px;
        }
        &_prev {
            left: 160px;
            @include tabletb {
                left: 140px;
            }
        }
    }
    &__i {
        display: inline-block;
        vertical-align: middle;
        margin-right: 40px;
        &:last-child {
            margin-right: 0;
        }
        @include phonem {
            margin-right: 10px;
        }
    }
    &__link {
        display: inline-block;
        vertical-align: middle;
        padding-top: 8px;
        margin-right: 5%;
        font-size: 17px;
        font-weight: bold;
        text-transform: uppercase;
    }
    &__or {
        color: $orange;
    }
}
